.city-info {
    position: relative;
    
    display: flex;
    padding: 1rem 2.3rem;
    background: #F6F9FC;
    border-radius: 1.2rem;
    width: 31rem;
    height: 6.3rem; 
    margin-right: 1.2rem;
    margin-left: auto;

    @media(max-width: $desktop_sm_width) {
        padding: 1rem;
    }

    @media(max-width: $tablet_width) {
        flex-direction: column;
        position: absolute;
        top: 1.4rem;
        right: 1.6rem;
        min-height: 12rem;

        padding: 1.6rem;
        margin-right: 0;
    }

    @media(max-width: 1280px) {
        width: fit-content;
        height: calc(100% - 3rem);
    }

    @media(max-width: 640px) {
        height: auto;
    }

    @media(max-width: 414px) {
        position: static;
        top: initial;
        right: initial;
        width: 100%;
    }

    &__choose {
        display: flex;
        flex-direction: column;
        padding-right: 2rem;
        border-right: 1px solid rgba(106, 150, 226, 0.54);

        @media(max-width: $tablet_width) {
            border-right: 0;
            border-bottom: 1px solid rgba(106, 150, 226, 0.54);
            padding-right: 0;
        }

        span {
            font-family: $font_sec-bold;
            font-size: 1.8rem;
            margin-bottom: 0.4rem;

            color: $color_darkblue;

            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
            width: 88px;

            @media(max-width: $desktop_sm_width) {
                font-size: 1.6rem;
                line-height: 1.9rem;
            }
        }

        a {
            font-family: $font_sec-bold;
            font-size: 1.4rem;
            line-height: 1.6rem;
            text-decoration-line: underline;

            color: $color_darkblue;
            transition: color $transition_rules;

            @media(max-width: $tablet_width) {
                margin-bottom: 0.4rem;

                font-size: 1.3rem;
                line-height: 1.5rem;
            }

            &:hover,
            &:focus {
                color: $color_blue;
            }
        }
    }

    &__phone {
        &-list {
            display: flex;
            flex-direction: column;
            padding-left: 2rem;

            @media(max-width: $tablet_width) {
                padding-left: 0;
                padding-top: 0.4rem;
            }
        }

        &-item {
            &:not(:last-child) {
                margin-bottom: 0.4rem;
            }
            
            a {
                font-family: $font_sec-bold;
                font-size: 1.8rem;
                line-height: 2.1rem;
    
                color: $color_darkblue;
                transition: color $transition_rules;

                @media(max-width: $desktop_sm_width) {
                    font-size: 1.6rem;
                    line-height: 1.9rem;
                }

                &:hover,
                &:focus {
                    color: $color_blue;
                }
            }
        }
    }
}